/* tat.css */

body, html {
  margin: 0;
  padding: 0;
  background-color: black; /* Sets the entire background to black */
  color: white;
  height: 100%;
  width: 100%;
  /* overflow: hidden; Prevents double scrollbars */
}

.tat-container {
  position: relative; /* To position the home icon correctly */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  box-sizing: border-box;
  background-color: black; /* Ensures the entire background remains black */
}

.home-icon {
  position: absolute;
  top: 20px;
  left: 20px;
  cursor: pointer;
  color: white;
  z-index: 1000; /* Ensure the icon is always on top */
}

.sets {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

.set-button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 1.2em;
  cursor: pointer;
  background-color: #444; /* Default background color */
  color: white;
  border: none;
  border-radius: 8px; /* Rounded corners */
}

.set-button:hover,
.set-button.selected {
  background-color: #555; /* Hover and selected background color */
}

.selected-set {
  margin-top: 20px;
}

.options {
  margin-bottom: 20px;
}

.option-button {
  padding: 10px 20px;
  background-color: #444;
  color: white;
  border: none;
  cursor: pointer;
  margin-right: 10px;
}

.option-button:hover {
  background-color: #555;
}

.start-button {
  background-color: #4CAF50; /* Green color similar to WAT */
}

.test {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: black;
}

.image-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  overflow: hidden; /* Prevent image overflow */
}

.test-image {
  width: 100%;
  height: 100%;
  object-fit: contain; /* Ensure the image fits within the container */
}

.blank-screen {
  width: 100%;
  height: 100%;
  background-color: black;
}

/* stopwatch */
.stopwatch {
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black background */
  padding: 5px; /* Padding around the stopwatch text */
  border-radius: 5px; /* Rounded corners for the background */
  color: white;
  text-align: center;
  font-size: 1rem;
}

/* Responsive adjustments */
@media only screen and (max-width: 768px) {
  .tat-container {
    padding: 10px; /* Reduce padding on smaller screens */
    padding-top: 60px; /* Add space for home icon */
  }

  .option-button {
    padding: 10px 16px; /* Adjust padding for smaller buttons */
    font-size: 0.9em; /* Decrease font size */
  }

  .home-icon {
    top: 10px;
    left: 10px;
  }
}
