.show-wat-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 60px 20px 20px;
  box-sizing: border-box;
  background-color: black;
  color: white;
}

.home-icon {
  position: absolute;
  top: 20px;
  left: 20px;
  cursor: pointer;
  color: white;
  z-index: 1000;
}

.sets {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.set-button {
  padding: 10px 20px;
  background-color: #444;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1em;
  border-radius: 10px; /* Add border-radius to make corners more curved */
  transition: background-color 0.3s ease;
}

.set-button:hover {
  background-color: #555;
}

.set-button.active {
  background-color: #007bff; /* Change to your desired active color */
  color: white;
}

.selected-set {
  margin-top: 20px;
}

.word-thumbnails {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.word-thumbnail {
  position: relative;
  padding: 10px;
  background-color: #333;
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 120px; /* Initial width */
  height: 120px; /* Initial height */
  transition: all 0.3s ease; /* Smooth transition for resizing */
}

.word-thumbnail p {
  margin: 0;
  font-size: 1.2em; /* Increase font size */
  font-weight: bold; /* Optionally increase font weight */
  white-space: nowrap; /* Prevent word wrapping */
  overflow: hidden; /* Hide overflow */
  text-overflow: ellipsis; /* Show ellipsis if overflow */
}

.word-index {
  position: absolute;
  bottom: -20px; /* Adjust spacing from thumbnail */
  left: 50%;
  transform: translateX(-50%);
  background-color: #555;
  color: white;
  padding: 8px; /* Increase padding for larger circle */
  border-radius: 50%;
  font-size: 1em; /* Increase font size */
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px; /* Increase width for larger circle */
  height: 30px; /* Increase height for larger circle */
}

/* Adjustments for larger thumbnails */
@media (min-width: 768px) {
  .word-thumbnail {
    width: 180px; /* Larger width for bigger screens */
    height: 180px; /* Larger height for bigger screens */
  }

  .word-index {
    bottom: -25px; /* Adjust circle position for larger thumbnails */
    width: 40px; /* Larger width for circle */
    height: 40px; /* Larger height for circle */
    font-size: 1.2em; /* Larger font size for circle */
  }
}

/* Mobile-specific adjustments */
@media (max-width: 480px) {
  h1 {
    font-size: 1.5em; /* Adjust title size for smaller screens */
    margin-top: 20px; /* Ensure there is space between the home icon and title */
    text-align: center;
  }

  .show-wat-container {
    padding: 80px 20px 20px; /* Increase top padding on small screens */
  }

  .home-icon {
    top: 10px; /* Adjust position if needed */
    left: 10px; /* Adjust position if needed */
  }
}
